import TWEEN from "@tweenjs/tween.js";
import Map from "../Map.js";
import { game } from "../game.js";
import {
	h,
	ref,
    reactive,
    defineComponent,
    onMounted,
    onUnmounted,
} from "../init/index.js";
import Plane, { PlaneInfo } from "../plane.js";
import EnemyPlane, { EnemyPlaneInfo } from "../enemyPlane.js";
import Bullet, { SelfBulletInfo, EnemyBulletInfo } from "../bullet.js";
import { moveBullets } from "../moveBullets";
import { stage } from "../config.js";
import { keyboardMove } from "../control";
import { moveEnemyPlane } from "../moveEnemyPlane";
import { hitTestRectangle } from "../utils/hitTestRectangle";
let hashCode = 0;
const createHashCode = () => {
	return hashCode++;
};
const killNumber = ref(0);
// function
const createSelfPlane = ({ x, y, speed }) => {
    const selfPlane = reactive({
		x,
		y,
		speed,
		width: PlaneInfo.width,
		height: PlaneInfo.height,
    });
    // 
    const { x: selfPlaneX, y: selfPlaneY } = keyboardMove({
        x: selfPlane.x,
        y: selfPlane.y,
        speed: selfPlane.speed,
    });
    // 
    let tween = new TWEEN.Tween({
		x,
		y,
    })
      .to({ y: y - 250 }, 500)
      .start();
    tween.onUpdate((obj) => {
		console.log('obj', obj);
		selfPlane.x = obj.x;
		selfPlane.y = obj.y;
    });
  
    const handleTicker = () => {
    	TWEEN.update();
    };
  
    onUnmounted(() => {
    	game.ticker.remove(handleTicker);
    });
  
    onMounted(() => {
    	game.ticker.add(handleTicker);
    });
    selfPlane.x = selfPlaneX;
    selfPlane.y = selfPlaneY;
    return selfPlane;
};
// function
const createEnemyPlanes = () => {
    //
    const createEnemyPlaneData = (x) => {
		return {
			x,
			y: -100,
			width: EnemyPlaneInfo.width,
			height: EnemyPlaneInfo.height,
			life: EnemyPlaneInfo.life,
		};
    };
  
    const enemyPlanes = reactive([]);
  
    setInterval(() => {
    	const x = Math.floor((1 + stage.width) * Math.random());
    	enemyPlanes.push(createEnemyPlaneData(x));
    }, 600);
    return enemyPlanes;
};
// 
const createSelfBullet = () => {
	// 
	const selfBullets = reactive([]);
	// 
	const addSelfBullet = (x, y) => {
		const id = createHashCode();
		const width = SelfBulletInfo.width;
		const height = SelfBulletInfo.height;
		const rotation = SelfBulletInfo.rotation;
		const dir = SelfBulletInfo.dir;
		selfBullets.push({ x, y, id, width, height, rotation, dir });
	};
	// 
	const destroySelfBullet = (id) => {
		const index = selfBullets.findIndex((info) => info.id == id);
		if (index !== -1) {
			selfBullets.splice(index, 1);
		}
	};
	return {
		selfBullets,
		addSelfBullet,
		destroySelfBullet,
	};
};
// 
const createEnemyPlaneBullets = () => {
	// 
	const enemyPlaneBullets = reactive([]);
	const addEnemyPlaneBullet = (x, y) => {
		const id = createHashCode();
		const width = EnemyBulletInfo.width;
		const height = EnemyBulletInfo.height;
		const rotation = EnemyBulletInfo.rotation;
		const dir = EnemyBulletInfo.dir;
		enemyPlaneBullets.push({ x, y, id, width, height, rotation, dir });
	};
	return {
		enemyPlaneBullets,
		addEnemyPlaneBullet,
	};
};
  
// 
const startFighting = ({selfPlane, enemyPlanes, selfBullets, enemyPlaneBullets, gameOverCallback}) => {
    const handleTicker = () => {
		moveBullets(selfBullets);
		moveBullets(enemyPlaneBullets);
        moveEnemyPlane(enemyPlanes);
        // 
		// 
		const hitSelfHandle = (enemyObject) => {
            const isIntersect = hitTestRectangle(selfPlane, enemyObject);
            if (isIntersect) {
                // 
                //  game over
                // 
                gameOverCallback && gameOverCallback();
            }
		};
		// 
        enemyPlanes.forEach((enemyPlane) => {
            hitSelfHandle(enemyPlane);
		});
		// 
		selfBullets.forEach((bullet, selfIndex) => {
			// 
			enemyPlanes.forEach((enemyPlane, enemyPlaneIndex) => {
			  	const isIntersect = hitTestRectangle(bullet, enemyPlane);
			  	if (isIntersect) {
					selfBullets.splice(selfIndex, 1);
					// 
					enemyPlane.life--;
					if (enemyPlane.life <= 0) {
						enemyPlanes.splice(enemyPlaneIndex, 1);
					}
			  	}
			});
			// 
			enemyPlaneBullets.forEach((enemyBullet, enemyBulletIndex) => {
				const isIntersect = hitTestRectangle(bullet, enemyBullet);
				if (isIntersect) {
					selfBullets.splice(selfIndex, 1);
					enemyPlaneBullets.splice(enemyBulletIndex, 1);
				}
			});
		});
		// 
		enemyPlaneBullets.forEach((enemyBullet, enemyBulletIndex) => {
			hitSelfHandle(enemyBullet);
		});
    };
    
    onUnmounted(() => {
        game.ticker.remove(handleTicker);
    });
    
    onMounted(() => {
        game.ticker.add(handleTicker);
    });
}
export default defineComponent({
    props: ["onNextPage"],
    setup(props) {
    	const selfPlane = createSelfPlane({
        	x: stage.width / 2 - 60,
        	y: stage.height,
        	speed: 7,
      	});
		const enemyPlanes = createEnemyPlanes();
		const {
			enemyPlaneBullets,
			addEnemyPlaneBullet,
		} = createEnemyPlaneBullets();
		const {
			selfBullets,
			addSelfBullet,
			destroySelfBullet,
		} = createSelfBullet();
      	startFighting({
			selfPlane,
			enemyPlaneBullets,
			enemyPlanes,
			selfBullets,
			gameOverCallback() {
				props.onNextPage('endPage');
			}
		});
      	return {
			addSelfBullet,
			destroySelfBullet,
			addEnemyPlaneBullet,
			selfBullets,
        	selfPlane,
			enemyPlanes,
			enemyPlaneBullets
      	};
    },
    render(ctx) {
		const createBullet = (info, index) => {
			return h(Bullet, {
			  key: "Bullet" + info.id,
			  x: info.x,
			  y: info.y,
			  id: info.id,
			  width: info.width,
			  height: info.height,
			  rotation: info.rotation,
			  dir: info.dir,
			  onDestroy({ id }) {
				ctx.destroySelfBullet(id);
			  },
			});
		};
        const createSelfPlaneCom = () => {
            return h(Plane, {
                x: ctx.selfPlane.x,
                y: ctx.selfPlane.y,
				speed: ctx.selfPlane.speed,
				onAttack({ x, y }) {
					ctx.addSelfBullet(x, y);
				},
            });
        };
        const createEnemyPlaneCom = (info, index) => {
            return h(EnemyPlane, {
                key: "EnemyPlane" + index,
                x: info.x,
                y: info.y,
                height: info.height,
				width: info.width,
				onAttack({ x, y }) {
					ctx.addEnemyPlaneBullet(x, y);
				},
            });
        };
        return h("Container", [
            h(Map),
            createSelfPlaneCom(),
			...ctx.enemyPlanes.map(createEnemyPlaneCom),
			...ctx.enemyPlaneBullets.map(createBullet),
			...ctx.selfBullets.map(createBullet)
        ]);
    }
});
  